.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  height: auto;
  padding: 32px 19px;
  display: flex;
}

.product-card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-size: cover;
  width: 160px;
  height: 134px;
}

.product-card__info {
  padding-top: 40px;
}

.product-card__title {
  color: #060b35;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  width: 166px;
  height: 36px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__price {
  justify-content: space-between;
  width: 166px;
  height: 18px;
  padding-bottom: 16px;
  display: flex;
}

.product-card__price-text {
  color: #616070;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__price-value {
  color: #060b35;
  text-align: right;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__code {
  color: #616070;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__reviews-count {
  color: #060b35;
  text-align: right;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__buy-button {
  text-align: center;
  color: #f3f3f3;
  background: #00acdc;
  border-radius: 5px;
  align-content: center;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.product-card__buy-button:hover {
  color: #00acdc;
  background-color: #f3f3f3;
  border: 1px solid #00acdc;
}

.item__title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.item__code {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__rating {
  text-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 17px;
  padding-top: 16px;
  display: flex;
}

.product-card__stars {
  display: flex;
}

.product-card__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.product-card__star:last-child {
  margin-right: 0;
}

.product-card__star:nth-child(n+5) {
  background-image: url("star.822d561e.svg");
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
